<template>
  <div class="kaoqin">
    <div class="nav-bar-white">
      <headerBar :header-data="headerData" />
    </div>
    <div class="kaoqin-head">
      <div class="kaoqin-userinfo-bg" />
      <div class="kaoqin-userinfo">
        <div class="userinfo-tx" @click="showImage(userInfo.tx_url?userInfo.tx_url:avatar)">
          <van-image
            :src="
              userInfo.tx_url?userInfo.tx_url:avatar"
            fit="cover"
            :round="true"
          />
        </div>
        <div class="userinfo-name">
          <div class="name">{{ userInfo.name }}</div>
          <div class="info-flex">
            <div class="info">
              <i class="iconfont icondailijigou" />
              {{ userInfo.bmmc }}
            </div>
            <div class="info">
              <i class="iconfont iconshouji" />
              {{ userInfo.sj }}
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="kaoqin-time"><i class="iconfont iconalarm_clock" /><em></em>{{ date }}</div>
    <div class="content">
      <div class="kaoqin-item">
        <div class="kaoqin-list">
          <div class="kaoqin-icon am" />
          <div class="kaoqin-type">
            <div v-for="(item,index) of amUpdateQdData" :key="index">
              <div v-if="item.worktype" class="type" @click="showPopupKqlx(item)">{{ QdShareDataList[item.worktype] }}  {{ item.worktype!=='zc'?item.address:'' }}</div>
            </div>
          </div>
          <div v-if="amUpdateQdData[0].id" class="kaoqin-state done"></div>
          <div v-else class="kaoqin-state "></div>
        </div>

        <div class="kaoqin-list">
          <div class="kaoqin-icon pm" />
          <div class="kaoqin-type">
            <div v-for="(items,indexs) of pmUpdateQdData" :key="indexs">
              <div v-if="items.worktype" class="type" @click="showPopupKqlx(items)">
                {{ QdShareDataList[items.worktype] }} {{ items.worktype!=='zc'?items.address:'' }}</div>
            </div>
          </div>
          <div v-if="pmUpdateQdData[0].id" class="kaoqin-state done"></div>
          <div v-else class="kaoqin-state "></div>
        </div>

      </div>
      <van-notice-bar v-show="showTip" left-icon="info-o" class="notice-bar-white" scrollable>
        ..
      </van-notice-bar>

      <div class="login-button">
        <!-- <van-button round type="info" @click="goKqtxl()"></van-button> -->
        <van-button round type="info" :disabled="isdisabled" @click="sendQdSj()"></van-button>
      </div>

    </div>

    <!--  begin -->
    <van-popup v-model="KqlxShow" round closeable position="bottom" :style="{ height: '85%' }">
      <div class="common-popup">
        <div class="popup-title"></div>
        <div class="popup-content">
          <div class="select-list select-list-line">
            <ul v-for="(item,index) of QdAmData" :key="index" @click="cheackQdType(index)">
              <li :class="checkIndex===index?'active':''">{{ item.title }}</li>
            <!-- <li class="active"></li>
              -->
            </ul>
          </div>
          <van-field
            v-if="showCsdd"
            v-model="csdd"
            label=""
            required
            placeholder=""
            :rules="[{ required: true, message: '' }]"
          />
          <div v-if="showCsdd"></div>
          <div v-if="showJjbg">{{ locationMsg.address1 }}</div>
          <van-field
            v-if="showSnbs"
            v-model="bsdd"
            label=""
            required
            placeholder=""
            :rules="[{ required: true, message: '' }]"
          />
        </div>
        <!--  begin -->
        <div v-if="showSnbs" class="foot-btn fixed">
          <ul>
            <li><van-button type="info" plain round size="large" @click="addSnType(0)"></van-button></li>
            <li><van-button type="info" round size="large" @click="addSnType(1)"></van-button></li>
          </ul>
        </div>
        <!--  end -->
        <!--  begin -->
        <div v-if="showCsdd" class="foot-btn fixed">
          <ul>
            <li><van-button type="info" plain round size="large" @click="addCcType(0)"></van-button></li>
            <li><van-button type="info" round size="large" @click="addCcType(1)"></van-button></li>
          </ul>
        </div>
        <!--  end -->
        <!--  begin -->
        <div v-if="showJjbg" class="foot-btn fixed">
          <ul>
            <li><van-button type="info" plain round size="large" @click="qr(0)"></van-button></li>
            <li><van-button type="info" round size="large" @click="qr(1)"></van-button></li>
          </ul>
        </div>
        <!--  end -->
      </div>
    </van-popup>
    <!--  end -->
    <div class="common-space" />
    <FooterNav ref="FooterNav" />
  </div>
</template>

<script land="script" scoped>
import { getUserName } from '@/common/js/catch'
import FooterNav from '@/components/footerNav/footerNav.vue'
import headerBar from '@/components/header/headerBar.vue'
import constants from '@/common/js/constants.js'
import { mapState } from 'vuex'
import { pageRouter } from '@/common/js/mixin.js'
import Loading from '@/components/spin/index.js'
import { ImagePreview } from 'vant'
import { JSEncrypt } from 'jsencrypt'
import { Base64 } from 'js-base64'

const loading = Loading.get({})
export default {

  components: {
    headerBar,
    FooterNav
  },
  mixins: [pageRouter],
  data() {
    return {
      btnState: true,
      checkIndex: '',
      date: '',
      headerData: {
        title: ''
      },
      avatar: require('@/assets/images/default-tx.png'), // 
      QdAmData: '',
      showTip: false, // 
      showSpinner: false, // 
      showCsdd: false, // 
      showSnbs: false, // 
      showJjbg: false, // 
      csdd: '', // 
      bsdd: '', // 
      checkPopupList: -1, // 
      showPopup: '', // 0 2--1 3--
      KqlxShow: false,
      isdisabled: false, // 
      gaodeDW: { // 
        lng: '',
        lat: ''
      },
      // 01APP
      amUpdateQdData: [{ id: '', dayType: '', times: 0.5, worktype: 'wzt', source: '1', address: ''
      }], // 
      pmUpdateQdData: [{ id: '', dayType: '', times: 0.5, worktype: 'wzt', source: '1', address: ''
      }], // 
      locationMsg: {}, // 
      getAddressType: '' //   '':  'jjbg'
    }
  },
  computed: {
    // 
    ...mapState({
      userInfo: state => state.mh.mhUserInfo
    })
  },
  mounted() {
    const _this = this // Vuethis
    this.timer = setInterval(() => {
      _this.date = _this.getNowTime() // date
    }, 1000)
    var self = this
    window['updateLocation1'] = function(value) {
      console.log('value======123==============', value)
      if (value.lon === '0' || value.lat === '0') {
        loading.hideLoading()
        self.$toast('')
        self.btnState = true
      } else {
        loading.hideLoading()
        if (self.amUpdateQdData[0].worktype === 'zc') {
          self.amUpdateQdData[0].address = value.address
        }
        if (self.pmUpdateQdData[0].worktype === 'zc') {
          self.pmUpdateQdData[0].address = value.address
        }
        self.goKqtxl()
      }
    }
    window['updateLocation'] = function(value) {
      if (self.getAddressType === 'jjbg') {
        console.info('-----', value.lon, value.lat, value.address)
        if (value.lon === '0' || value.lat === '0') {
          _this.$toast('')
          self.btnState = true
        } else {
          _this.locationMsg.lng = value.lon
          _this.locationMsg.lat = value.lat
          _this.locationMsg.address1 = value.address
        }
      } else {
        console.info('-----', self.gaodeDW.lng, self.gaodeDW.lat, '-----', value.lon, value.lat)
        if (value.lon === '0' || value.lat === '0') {
          loading.hideLoading()
          self.$toast('')
          self.btnState = true
        } else {
          if (self.GetDistance(self.userInfo.dzwlbj, self.gaodeDW.lng, self.gaodeDW.lat, value.lon, value.lat)) {
            loading.hideLoading()
            self.goKqtxl()
          } else {
            loading.hideLoading()
            self.$toast('')
            self.btnState = true
          }
        }
      }
    }
    // window['isAddressPermission'] = function(value) { // 
    //   console.log('-------', value)
    //   if (value === '0') { // 
    //     self.getLocation()
    //     self.getAddressType = 'jjbg'
    //     self.showJjbg = true // 
    //   } else {
    //     self.$toast('')
    //   }
    // }
  },
  created() {
    this.$store.commit('router/SET_Foot_Num', 2)
    const _this = this // Vuethis
    _this.date = _this.getNowTime()
    this.timer = setInterval(() => {
      _this.date = _this.getNowTime() // date
    }, 1000)

    this.QdAmData = this.userInfo.dzwlqx === '1' || this.userInfo.dzwlqx === 1 ? constants.QdAmData_dzwl : constants.QdAmData_zc // 
    console.info('dzwlqx------', this.userInfo.dzwlqx)
    console.info('QdAmData------', this.QdAmData)
    this.QdShareDataList = constants.QdShareDataList // 
    // this.QdAmData_dzwl = constants.QdAmData_dzwl // 
    this.QdPmData = constants.QdPmData // 
    if (this.userInfo.dzwlqx === '1' || this.userInfo.dzwlqx === 1) {
      this.showTip = true
    }
    if (this.userInfo.yhzb !== null && this.userInfo.yhzb !== undefined && this.userInfo.yhzb !== '') {
      if (this.userInfo.yhzb.lng !== '' && this.userInfo.yhzb.lat !== '') {
        var gaode = this.bd_decrypt(this.userInfo.yhzb.lng, this.userInfo.yhzb.lat)
        this.gaodeDW.lng = gaode.lng
        this.gaodeDW.lat = gaode.lat
      }
    }
    this.getKqData()
  },
  methods: {
    getLocation() {
      const pin = {
        'type': '1'
      }
      if (process.env.NODE_ENV !== 'development') {
        try {
          window.myapp.getDistance(JSON.stringify(pin))
        } catch (err) {
          console.log(err)
        }
        this.$bridge.callhandler('getDistance', JSON.stringify(pin), (data) => {
          const datas = JSON.parse(data)
          this.locationMsg.lng = datas.lng
          this.locationMsg.lat = datas.lat
          this.locationMsg.address1 = datas.address
          console.info('-----', datas.lng, datas.lat, datas.address)
          console.info('getDistance------', this.locationMsg.address)
        })
      } else {
        this.$toast('')
      }
    },
    showImage(imagePath) {
      ImagePreview({
        images: [
          imagePath
        ],
        startPosition: 0,
        onClose() {
          // do something
        }
      })
    },
    getNowTime() {
      const date = new Date()
      let hours = ''
      let minute = ''
      let second = ''
      if (date.getHours() < 10) {
        hours = '0' + date.getHours()
      } else {
        hours = date.getHours()
      }
      if (date.getMinutes() < 10) {
        minute = '0' + date.getMinutes()
      } else {
        minute = date.getMinutes()
      }
      if (date.getSeconds() < 10) {
        second = '0' + date.getSeconds()
      } else {
        second = date.getSeconds()
      }
      return date.getFullYear() + '' + (date.getMonth() + 1) + '' + date.getDate() + '' + '  ' + hours + ':' + minute + ':' + second
    },
    getKqData() {
      this.$store.dispatch('erp/querySignAction', {
        userId: this.userInfo.id
        // userId: '5a9c10a46b9689cb016bb11e3fac1556'
      }).then((res) => {
        console.info('res------', res)
        const signData = res.sign
        for (let index = 0; index < signData.length; index++) {
          const element = signData[index]
          this.handleData(element)
        }
        console.info('this.pmUpdateQdData------', this.pmUpdateQdData)
        console.info('this.amUpdateQdData------', this.amUpdateQdData)
        // this.$toast.success('')
        // // this.$router.push({ name: 'MhHome' })
        // this.$router.push({ name: 'KqTj' })
      }).catch((error) => {
        this.$toast(error)
      })
    },
    // 
    showPopupKqlx(item) {
      if (item.worktype === 'qj') {
        this.$toast.fail('')
        return
      }
      if (item.worktype === 'qjspz') {
        this.$toast.fail('')
        return
      }
      // 1  0
      if (this.userInfo.dzwlqx === 1 || this.userInfo.dzwlqx === '1') { //  
        if (item.dayType === '' && item.worktype !== 'wzt') {
          this.$toast.fail('')
          return
        }
      }

      if (this.checkIndex !== '') {
        if (this.QdAmData[this.checkIndex].title_en === 'cc') {
          this.showCsdd = true
          this.showJjbg = false
          this.showSnbs = false
        } else {
          this.showCsdd = false
        }
        if (this.QdAmData[this.checkIndex].title_en === 'jjbg') {
          this.showJjbg = true
          this.showCsdd = false
          this.showSnbs = false
        } else {
          this.showJjbg = false
        }
        if (this.QdAmData[this.checkIndex].title_en === 'bs') {
          this.showJjbg = false
          this.showCsdd = false
          this.showSnbs = true
        } else {
          this.showSnbs = false
        }
      } else {
        this.showJjbg = false
        this.showCsdd = false
        this.showSnbs = false
      }

      // 0-1- 2-3-
      if (item.dayType === '') {
        this.QdAmData = this.userInfo.dzwlqx === '1' || this.userInfo.dzwlqx === 1 ? constants.QdAmData_dzwl : constants.QdAmData_zc // 
        if (item.worktype === 'wc') {
          this.amUpdateQdData[0].times = 0.5
          // this.amUpdateQdData[1].worktype = ''
          this.amUpdateQdData.splice(1, 1)
          return
        }
        this.showPopup = 0
      } else if (item.dayType === '') {
        this.QdAmData = constants.QdPmData
        if (item.worktype === 'wc') {
          this.pmUpdateQdData[0].times = 0.5
          // this.pmUpdateQdData[1].worktype = ''
          this.pmUpdateQdData.splice(1, 1)
          return
        }
        this.showPopup = 1
      }

      // this.showPopup = type
      // if (item.worktype === 'wc') {
      //   this.updateQdData[1].worktype = '' // 
      // } else if (type === 3) {
      //   this.updateQdData[3].worktype = '' // 
      // }
      this.KqlxShow = true
    },
    // 
    qr(type) {
      if (type === 0) {
        this.locationMsg.address1 = ''
        this.getLocation()
      } else {
        if (this.locationMsg.address1) {
          if (this.showPopup === 0) {
            this.amUpdateQdData[0].address = this.locationMsg.address1
            this.amUpdateQdData[0].source = 1
            this.amUpdateQdData[0].worktype = 'jjbg'
          } else {
            this.pmUpdateQdData[0].worktype = 'jjbg'
            this.pmUpdateQdData[0].source = 1
            this.pmUpdateQdData[0].address = this.locationMsg.address1
          }
          this.KqlxShow = false
        } else {
          this.$toast('')
        }
      }
    },
    addSnType(index) {
      // 
      if (index === 0) {
        this.bsdd = ''
        return
      }
      if (this.checkPopupList < 0) {
        this.$toast.fail('')
        return
      }
      if (this.bsdd.length === 0) {
        this.$toast.fail('')
        return
      }
      if (this.showPopup === 0) {
        this.amUpdateQdData[0].address = this.bsdd
        this.amUpdateQdData[0].source = 1
        this.amUpdateQdData[0].worktype = 'bs'
      } else {
        this.pmUpdateQdData[0].worktype = 'bs'
        this.pmUpdateQdData[0].source = 1
        this.pmUpdateQdData[0].address = this.bsdd
      }
      this.KqlxShow = false
    },
    // 
    addCcType(index) {
      // 
      if (index === 0) {
        this.csdd = ''
        return
      }
      if (this.checkPopupList < 0) {
        this.$toast.fail('')
        return
      }
      if (this.csdd.length === 0) {
        this.$toast.fail('')
        return
      }
      if (this.showPopup === 0) {
        // this.amUpdateQdData[0].title = '---' + this.csdd
        this.amUpdateQdData[0].address = this.csdd
        this.amUpdateQdData[0].source = 1
        this.amUpdateQdData[0].worktype = 'cc'
      } else {
        // this.pmUpdateQdData[0].worktype = '---' + this.csdd
        this.pmUpdateQdData[0].worktype = 'cc'
        this.pmUpdateQdData[0].source = 1
        this.pmUpdateQdData[0].address = this.csdd
      }
      this.KqlxShow = false
    },
    handleData(element) {
      if (element.mark_noon === '') {
        if (this.amUpdateQdData[0].id === '') {
          this.amUpdateQdData[0].id = element.id
          this.amUpdateQdData[0].times = element.days_span
          this.amUpdateQdData[0].address = element.address
          this.amUpdateQdData[0].source = element.source
          this.amUpdateQdData[0].worktype = element.task_type
        } else {
          const wc = { id: element.id, dayType: '', times: element.days_span, worktype: element.task_type, source: element.source, address: element.address
          }
          this.amUpdateQdData.push(wc)
        }
        // this.amUpdateQdData.push(newObj)
      } else {
        if (this.pmUpdateQdData[0].id === '') {
          this.pmUpdateQdData[0].id = element.id
          this.pmUpdateQdData[0].times = element.days_span
          this.pmUpdateQdData[0].address = element.address
          this.pmUpdateQdData[0].source = element.source
          this.pmUpdateQdData[0].worktype = element.task_type
        } else {
          const wc = { id: element.id, dayType: '', times: element.days_span, worktype: element.task_type, source: element.source, address: element.address
          }
          this.pmUpdateQdData.push(wc)
        }
      }
    },
    // 
    cheackQdType(index) {
      this.getAddressType = ''
      this.checkIndex = index
      // console.info('showPopup==A==', this.showPopup)
      // 

      if (this.QdAmData[index].title_en === 'wc') {
        if (this.amUpdateQdData[0].id === '' || this.pmUpdateQdData[0].id === '') {
          this.$toast.fail('')
          return
        }
        if (this.showPopup === 0) { // 
          if (this.amUpdateQdData[0].worktype !== 'qj' && this.amUpdateQdData[0].worktype !== 'tx') {
            // 
            // this.amUpdateQdData[1].worktype = 'wc'
            this.amUpdateQdData[0].times = 0.25
            const wc = { id: '', dayType: '', times: 0.25, worktype: 'wc', source: '1', address: ''
            }
            if (this.amUpdateQdData.length === 1) {
              this.amUpdateQdData.push(wc)
            }
          } else {
            this.$toast.fail('')
          }
        } else {
          if (this.pmUpdateQdData[0].worktype !== 'qj' && this.pmUpdateQdData[0].worktype !== 'tx') {
            // 
            this.pmUpdateQdData[0].times = 0.25
            const wc = { id: '', dayType: '', times: 0.25, worktype: 'wc', source: '1', address: ''
            }
            if (this.pmUpdateQdData.length === 1) {
              this.pmUpdateQdData.push(wc)
            }
          } else {
            this.$toast.fail('')
          }
        }
        this.KqlxShow = false
      } else if (this.QdAmData[index].title_en === 'cc') { // 
        //  
        if (this.amUpdateQdData.length > 1) {
          this.amUpdateQdData.splice(1.1)
        }
        //  
        if (this.pmUpdateQdData.length > 1) {
          this.pmUpdateQdData.splice(1.1)
        }
        this.checkPopupList = index
        this.showCsdd = true // 
        this.showJjbg = false
        this.showSnbs = false
      } else if (this.QdAmData[index].title_en === 'jjbg') { // 
        console.log('this.locationMsg==================', this.locationMsg)
        this.getLocation()
        this.getAddressType = 'jjbg'
        this.showJjbg = true // 
        this.showCsdd = false
        this.showSnbs = false
        this.checkPopupList = index
        // if (process.env.NODE_ENV !== 'development') {
        //   try {
        //     window.myapp.isAddressPermission('REQUEST_LOCATION')
        //   } catch (err) {
        //     console.log(err)
        //   }
        //   this.$bridge.callhandler('isAddressPermission', '', (data) => {

        //   })
        //   this.checkPopupList = index
        // } else {
        //   this.$toast('')
        //   return
        // }
      } else if (this.QdAmData[index].title_en === 'bs') {
        this.checkPopupList = index
        this.showSnbs = true
        this.showCsdd = false // 
        this.showJjbg = false
      } else { // 
        this.KqlxShow = false
        console.log('this.QdAmData[index]============', this.checkIndex, this.QdAmData)
        if (this.showPopup === 0) { // 
          this.amUpdateQdData[0].worktype = this.QdAmData[index].title_en
          this.amUpdateQdData[0].source = 1
          this.amUpdateQdData[0].address = ''
        } else {
          this.pmUpdateQdData[0].worktype = this.QdAmData[index].title_en
          this.pmUpdateQdData[0].source = 1
          this.pmUpdateQdData[0].address = ''
        }
      }
      console.info('amUpdateQdData==A==', this.amUpdateQdData)
      console.info('pmUpdateQdData==A==', this.pmUpdateQdData)
    },
    // 
    sendQdSj() {
      if (!this.btnState) {
        return
      }
      this.btnState = false
      if (this.amUpdateQdData[0].worktype === 'wzt') {
        this.amUpdateQdData[0].worktype = ''
      }
      if (this.pmUpdateQdData[0].worktype === 'wzt') {
        this.pmUpdateQdData[0].worktype = ''
      }
      // 1 0
      var str = this.userInfo.yhzb
      console.log('123==================1',this.amUpdateQdData, this.pmUpdateQdData);
      // 
      for (let i = 0; i < this.amUpdateQdData.length; i++) {
        if(this.amUpdateQdData[i].worktype == 'wc'){
          this.goKqtxl()
          return
        }
      }
      for (let i = 0; i < this.pmUpdateQdData.length; i++) {
        if(this.pmUpdateQdData[i].worktype == 'wc'){
          this.goKqtxl()
          return
        }
      }

      if (this.userInfo.dzwlqx === 1 || this.userInfo.dzwlqx === '1') { //  
        if (this.amUpdateQdData[0].worktype === 'zc') {
          console.info('str-----', str)
          if (str === null || str === undefined || str === '') {
            this.$toast.fail('')
            this.btnState = true
            return
          }
          const pin = { 'baseLat': this.userInfo.yhzb.lat,
            'baseLng': this.userInfo.yhzb.lng,
            'dzwlbj': this.userInfo.dzwlbj, // 
            // 'dzwlbj': 300, // 
            'type': '0'
          }
          loading.showLoading()

          if (process.env.NODE_ENV !== 'development') {
            try {
              window.myapp.getDistance(JSON.stringify(pin))
            } catch (err) {
              console.log(err)
            }
            this.$bridge.callhandler('getDistance', JSON.stringify(pin), (data) => {
              const datas = JSON.parse(data)
              // var gaode = this.bd_decrypt(datas.lng, datas.lat)
              // var lnglat = new AMap.LngLat(117.186602, 34.261579)// 
              console.info('-----', this.gaodeDW.lng, this.gaodeDW.lat, '-----', datas.lng, datas.lat)
              if (this.GetDistance(this.userInfo.dzwlbj, this.gaodeDW.lng, this.gaodeDW.lat, datas.lng, datas.lat)) {
                loading.hideLoading()
                this.goKqtxl()
              } else {
                loading.hideLoading()
                this.$toast('')
                this.btnState = true
              }
              console.info('getDistance------', datas)
            })
          } else {
            this.btnState = true
            this.$toast('')
          }
        } else {
          this.goKqtxl()
        }
      } else {
        // const pin = { 'baseLat': '',
        //   'baseLng': '',
        //   'dzwlbj': '', // 
        //   // 'dzwlbj': 300, // 
        //   'type': '2'
        // }
        // loading.showLoading()
        // if (process.env.NODE_ENV !== 'development') {
        //   try {
        //     window.myapp.getDistance(JSON.stringify(pin))
        //   } catch (err) {
        //     console.log(err)
        //   }
        //   this.$bridge.callhandler('getDistance', JSON.stringify(pin), (value) => {
        //     loading.hideLoading()
        //     if (value.lon === '0' || value.lat === '0') {
        //       loading.hideLoading()
        //       this.$toast('')
        //       this.btnState = true
        //     } else {
        //       loading.hideLoading()
        //       if (this.amUpdateQdData[0].worktype === 'zc') {
        //         if (!this.amUpdateQdData[0].address) {
        //           this.amUpdateQdData[0].address = value.address
        //         }
        //       }
        //       if (this.pmUpdateQdData[0].worktype === 'zc') {
        //         if (!this.pmUpdateQdData[0].address) {
        //           this.pmUpdateQdData[0].address = value.address
        //         }
        //       }
        //       this.goKqtxl()
        //     }
        //   })
        // } else {
        //   this.btnState = true
        //   loading.hideLoading()
        //   this.$toast('')
        // }
        this.goKqtxl()
      }
      // console.info('updateQdData--11--', testArray)
    },
    //  lat,lng
    GetDistance(dis, lat1, lng1, lat2, lng2) {
      var radLat1 = lat1 * Math.PI / 180.0
      var radLat2 = lat2 * Math.PI / 180.0
      var a = radLat1 - radLat2
      var b = lng1 * Math.PI / 180.0 - lng2 * Math.PI / 180.0
      var s = 2 * Math.asin(Math.sqrt(Math.pow(Math.sin(a / 2), 2) +
      Math.cos(radLat1) * Math.cos(radLat2) * Math.pow(Math.sin(b / 2), 2)))
      s = s * 6378.137 // EARTH_RADIUS;
      s = Math.round(s * 10000)
      console.info('s-------------', s)
      console.info('dis-------------', dis)
      const dd = dis + 500
      console.info('dd-------------', dd)
      if (dd >= s) {
        return true
      } else {
        return false
      }
    },
    // 
    bd_decrypt(bd_lng, bd_lat) {
      var X_PI = Math.PI * 3000.0 / 180.0
      var x = bd_lng - 0.0065
      var y = bd_lat - 0.006
      var z = Math.sqrt(x * x + y * y) - 0.00002 * Math.sin(y * X_PI)
      var theta = Math.atan2(y, x) - 0.000003 * Math.cos(x * X_PI)
      var gg_lng = z * Math.cos(theta)
      var gg_lat = z * Math.sin(theta)
      return { lng: gg_lng, lat: gg_lat }
    },
    // 
    goKqtxl() {

      var arrys = this.amUpdateQdData.concat(this.pmUpdateQdData)
      console.log('123==================2',arrys);

      const arr = []
      const datas = { 'params': arrys,
        'username': getUserName(),
        'userId': this.userInfo.id
        // 'userId': '5a9c10a46b9689cb016bb11e3fac1556'
      }
      this.isdisabled = true
      arr.push(datas)

      const encryptor = new JSEncrypt()
      encryptor.setPublicKey(this.$store.state.mh.jmkey)
      const encryptedArr = []

      const baseStr = JSON.stringify(arr)
      console.log('', baseStr)

      if (baseStr.length > 10) {
        const num = baseStr.length / 10
        for (let i = 0; i <= num; i++) {
          console.log('===========', baseStr.slice(i * 10, (i + 1) * 10))
          const jmstr = encryptor.encrypt(baseStr.slice(i * 10, (i + 1) * 10))
          encryptedArr.push(jmstr)
        }
      }
      console.log('', encryptedArr)
      this.$store.dispatch('erp/submitSignInfoAction', {
        userid: this.userInfo.id,
        data: encryptedArr,
        timestamp: new Date().getTime()
      }).then((res) => {
        this.isdisabled = false

        console.info('res------', res)
        this.amUpdateQdData = [{ id: '', dayType: '', times: 0.5, worktype: 'wzt', source: '1', address: ''
        }] // 
        this.pmUpdateQdData = [{ id: '', dayType: '', times: 0.5, worktype: 'wzt', source: '1', address: ''
        }]
        const signData = res.data.sign
        for (let index = 0; index < signData.length; index++) {
          const element = signData[index]
          this.handleData(element)
        }
        this.$toast(res.text)
        this.btnState = true
        // // this.$router.push({ name: 'MhHome' })
        // this.$router.push({ name: 'KqTj' })
      }).catch((error) => {
        this.btnState = true
        this.isdisabled = false
        this.$toast(error)
      })
    }
  }
}
